.Window {
    text-align: left;
    z-index: 200;
    top: 0;
    left: 0;
    position: fixed;
    height: 100vh;
    width: 100%;
    background: rgba(0, 0, 0, 0.836);
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.3);
    -webkit-transform: translateX(-110%);
            transform: translateX(-110%);
    -webkit-transition: -webkit-transform 0.3s ease-in-out;
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
    color: white;
    overflow-x:hidden;  
    /* text-align: center; */
}

.Blur {
    -webkit-filter: blur(4px);
            filter: blur(4px);
    -webkit-transition: -webkit-filter 0.6s ease-in-out;
    transition: -webkit-filter 0.6s ease-in-out;
    transition: filter 0.6s ease-in-out;
    transition: filter 0.6s ease-in-out, -webkit-filter 0.6s ease-in-out;
}

.Blur.off {
    -webkit-filter: blur(0px);
            filter: blur(0px);
}

.Window-x {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 100;
    /* cursor: pointer;; */
}

.Window-x:hover {
    cursor: pointer;
    color: transparent;
    text-shadow: 0 0 0 rgb(255, 156, 2);
}

.Window-x:hover::after,
.Window-x::after {
    content: "\2716";
    font-size: 30px;
}

.Window.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.window-right {
    background: #1d1d1d;
}

