/* custom radio/checkbox */

.input-container {
    display: flex;
    align-items: center;
    flex-direction: row;
    cursor: pointer;
    margin: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.input-container p {
    margin: 0 0 1px 0;
}

.input-container input {
    display: none;
}

.input-container .radio-button {
    display: inline-block;
    width: 25px;
    height: 25px;
    background: none;
    border: 2px solid #858586;
    border-radius: 50%;
}

.input-container .checkbox-button {
    display: inline-block;
    width: 12px;
    height: 12px;
    background: none;
    border: 1px solid #ffffff;
    border-radius: 1px;
}

.radio-button-container {
    position: relative;
    height: 25px;
    width: 25px;
    margin-left: 5px;
}

.checkbox-button-container {
    display: flex;
    position: relative;
    height: inherit;
    width: inherit;
}

.input-container input:checked~div .radio-button:after {
    position: absolute;
    content: "";
    border-radius: 50%;
    height: 10px;
    width: 10px;
    top: 50%;
    left: 50%;
    background: #858586;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.input-container input:checked~div .checkbox-button {
    background: url('data:image/svg+xml;base64,') 50% 50% no-repeat;
    background-size: 100%;

}


